











export default {
    name     : 'animated-background',
    props    : [],
    data     : () -> {
        backgrounds_opacity_handle : null
        background_1_transparency_class : 'non-transparent'
        background_2_transparency_class : 'transparent'
        background_3_transparency_class : 'transparent'
        background_2_direction          : false  # true - increase opacity, false - decrease
        background_3_direction          : true
    },
    computed : {},
    methods  : {
        change_backgrounds_opacity             : () ->
            this.$data.background_1_transparency_class = 'transparent'

            if this.$data.background_2_direction
                this.$data.background_2_direction          = false
                this.$data.background_2_transparency_class = 'transparent'
            else
                this.$data.background_2_direction          = true
                this.$data.background_2_transparency_class = 'non-transparent'

            if this.$data.background_3_direction
                this.$data.background_3_direction          = false
                this.$data.background_3_transparency_class = 'transparent'
            else
                this.$data.background_3_direction          = true
                this.$data.background_3_transparency_class = 'non-transparent'

        emit_event_night_image_disappeared : () ->
            this.$emit 'night-image-disappeared'

        continuosly_change_backgrounds_opacity : () ->
            five_seconds = 5000
            six_seconds  = 6000
            this.$data.backgrounds_opacity_handle = setInterval this.change_backgrounds_opacity, five_seconds
            setTimeout this.emit_event_night_image_disappeared, six_seconds

        stop_changing_backgrounds_opacity      : () ->
            clearInterval this.$data.backgrounds_opacity_handle
    },
    # lifecycle hooks
    mounted       : () ->
        this.continuosly_change_backgrounds_opacity()
    beforeDestroy : () ->
        this.stop_changing_backgrounds_opacity()
}
