

















export default {
    name     : 'startup-logo',
    props    : [],
    data     : () -> {
        class_for_logo_transparency      : 'transparent'
        class_for_component_transparency : 'non-transparent'
    },
    computed : {},
    methods  : {
        make_logo_slowly_appeared : () ->
            this.$data.class_for_logo_transparency = 'non-transparent'
        make_logo_slowly_disappeared : () ->
            this.$data.class_for_logo_transparency = 'transparent'
        make_all_component_slowly_disappeared : () ->
            this.$data.class_for_component_transparency = 'transparent'
        trigger_startup_logo_appeared_event : () ->
            this.$emit('startup-logo-appeared')
        trigger_startup_logo_disappeared_event : () ->
            this.$emit('startup-logo-disappeared')
        skip_startup_logo_on_mouse_click : () ->
            this.trigger_startup_logo_appeared_event()
            this.make_all_component_slowly_disappeared()
            setTimeout this.trigger_startup_logo_disappeared_event, 1000
    },
    # lifecycle hooks
    mounted : () ->
        setTimeout this.make_logo_slowly_appeared, 1000
        setTimeout this.trigger_startup_logo_appeared_event, 5000
        setTimeout this.make_logo_slowly_disappeared, 5000
        setTimeout this.make_all_component_slowly_disappeared, 6000
        setTimeout this.trigger_startup_logo_disappeared_event, 7000
}
