















import AnimatedBackground from '@/components/animated-background'

export default {
    name       : 'main-menu'
    props      : []
    data       : () -> {
        herbalist_logo_color_override  : 'herbalist-logo-night'  # make it empty line to make it green again
        main_menu_items_color_override : 'main-menu-item-night'
        }
    computed   : {}
    components : { 'animated-background': AnimatedBackground}
    methods    : {
        set_main_menu_items_green : () ->
            this.$data.herbalist_logo_color_override  = ''
            this.$data.main_menu_items_color_override = ''
        button_pressed_artworks : () ->
            this.$emit('button-pressed-artworks')
    }
}
