



































import {
    do_on_window_size_change,
    dont_do_on_window_size_change,
}                     from '@/helpers/hooks.coffee'
import MainMenu       from '@/components/main-menu.vue'
import artworks from '@/components/artworks.vue'
import StartupLogo    from '@/components/startup-logo.vue'


get_new_width_and_height_to_be_16x9 = () ->
    base_width  = window.innerWidth
    base_height = window.innerHeight
    new_width   = null
    new_height  = null

    should_be_larger_than = 16 / 9
    width_is_larger       = base_width > (base_height * should_be_larger_than)

    if width_is_larger
        new_width = (base_height * 16) / 9
    else
        new_height = (base_width * 9) / 16

    current_zoom = window.devicePixelRatio

    return {
        width  : (new_width  or base_width)  * current_zoom  + 'px',
        height : (new_height or base_height) * current_zoom  + 'px',
    }


get_app_style = () ->
    new_width_and_height = get_new_width_and_height_to_be_16x9()
    return new_width_and_height


center_app_to_body = () ->
    ###
    Centers app according body.
    Also fixes:
        If body has 'height: 100vh' and you zoom-out - all is well, if you zoom-in,
        than bottom part of body's content will be cutted out.
        If body has 'height: 100%', and you zoom-in - all is well, but when you zoom-out,
        than body reduce it's size and app is not centered on screen any more.
        So we need to make body's height be 100vh if zoomed-out, and 100% if zoomed-in.
    ###
    body       = document.querySelector 'body'
    zoomed_out = window.devicePixelRatio > 1
    if zoomed_out
        body.style.height = '100%'
    else
        body.style.height = '100vh'


export default {
    #17el         : '#app',
    name       : 'app'
    components : {
        MainMenu,
        StartupLogo,
        artworks,
    },
    data     : () -> {
            app_style : get_app_style()
            render_MainMenu       : false
            render_StartupLogo    : true
            render_OpenedArtworks : false
        },
    methods  : {
        re_render : () ->
            center_app_to_body()
            this.$data.app_style = get_app_style()
        render_only : (prop_responsible_for_rendering_needed_component) ->
            all_props_responsible_for_rendering = Object.keys(this.$data).filter((prop) -> prop.startsWith('render_'))
            for prop in all_props_responsible_for_rendering
                this.$data[prop] = false
            this.$data[prop_responsible_for_rendering_needed_component] = true
        render_main_menu       : (event) -> this.render_only('render_MainMenu')
        render_opened_artworks : (event) -> this.render_only('render_OpenedArtworks')
    }
    computed : {},
    mounted       : () ->
        center_app_to_body()
        do_on_window_size_change(this.re_render)
    beforeDestroy : () ->
        dont_do_on_window_size_change(this.re_render)
}
